.l-header .nav-list {
  display: flex;
  align-items: center;
  list-style: none;
  text-transform: uppercase; }

.l-header .nav-list__item:not(:last-child) {
  margin-right: 1rem; }

.l-header .nav-list__item-link {
  text-decoration: none;
  color: #fff;
  text-shadow: 1px 2px 3px rgba(0, 0, 0, 0.404);
  font-style: 1rem;
